.Education{
    width: 100%;
    height: 100vh;
}

.Dark{
    background-image: linear-gradient(to bottom,#271f49, #1b161e);
}

.Light{
    background-image: linear-gradient(to bottom, #719498, #9ce2dc93);
}

.Container{
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.Frame{
    width: 45%;
    margin: 2%;
    /* border: 5px black;
    border-style: dashed; */
    border-radius: 7%;
    box-sizing: border-box;
}

.Separator{
    margin-top: 20px;
    height: 100%;
    width: 0.2%;
    background-color: black;
}

.Dark .Separator{
    background-color: white;
}

.LogoContainer{
    width: 100%;
    height: 30%;
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
    display: flex;
    justify-content: center;
}

.Dark .LogoContainer{
    border-radius: 50%;
    background: radial-gradient(closest-side, #e6e0e0d7,#241d3e);
}

.FTNLogo{
    width: auto;
}

.AALogo{
    padding: 30px;
    width: auto;
}

.Institution{
    height: 20%;
    padding-top: 20px;
}

.Direction{
    height: 20%;
}

.Date{
    height: 20%;
    text-align: center;
}


@media (max-width: 500px) {
    .Container{
        justify-content: space-between;
        flex-direction: column;
        margin: 0 10px;
    }
    
    .Frame{
        margin:0;
        height: 47%;
        width: 100%;
        /* border: 2px dashed black;
        border-radius: 7%; */
        box-sizing: border-box;
    }
    
    .LogoContainer{
        height: 35%;
    }

    .AALogo{
        padding: 15px;
    }

    .Separator{
        margin: 0 auto;
        margin-bottom: 20px;
        width: 80%;
        height: 0.2%;
    }
}